"use client"

import { useEffect, useState } from "react"
import { useParams, useRouter } from "next/navigation"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Checkbox } from "@/components/ui/checkbox"
import { Button } from "@/components/ui/button"
import { useToast } from "@/components/ui/use-toast" // Corrigido o caminho de importação
import { BackButton } from "@/components/ui/BackButton"
import { usePermission } from "@/contexts/PermissionContext"
import usuarioService from "@/services/usuarioService" // Importando o serviço completo

export default function PermissoesUsuarioPage() {
  const { id } = useParams()
  const router = useRouter()
  const { toast } = useToast()
  const { hasPermission } = usePermission()
  const [usuario, setUsuario] = useState<any>(null)
  const [permissoes, setPermissoes] = useState<string[]>([])
  const [permissoesSelecionadas, setPermissoesSelecionadas] = useState<string[]>([])
  const [carregando, setCarregando] = useState(true)
  const [salvando, setSalvando] = useState(false)
  const [erro, setErro] = useState<string | null>(null)
  const [timeoutId, setTimeoutId] = useState<NodeJS.Timeout | null>(null)
  const [tentativasCarregamento, setTentativasCarregamento] = useState(0)

  // Verificar permissão
  useEffect(() => {
    if (!hasPermission("USUARIOS_PERMISSOES")) {
      toast({
        title: "Acesso negado",
        description: "Você não tem permissão para gerenciar permissões de usuários.",
        variant: "destructive",
      })
      router.push("/acesso-negado")
    }
  }, [hasPermission, router, toast])

  // Carregar dados do usuário e permissões
  useEffect(() => {
    let isMounted = true
    let loadTimeout: NodeJS.Timeout | null = null

    const carregarDados = async () => {
      if (!id || !hasPermission("USUARIOS_PERMISSOES")) return

      setCarregando(true)
      setErro(null)

      try {
        // Definir um timeout para evitar carregamento infinito
        loadTimeout = setTimeout(() => {
          if (isMounted) {
            setErro("Tempo limite excedido ao carregar permissões. Tente novamente.")
            setCarregando(false)
          }
        }, 10000) // 10 segundos de timeout

        // Carregar dados do usuário
        const dadosUsuario = await usuarioService.buscarUsuario(Number(id))
        if (!isMounted) return

        if (!dadosUsuario) {
          setErro("Usuário não encontrado")
          setCarregando(false)
          if (loadTimeout) clearTimeout(loadTimeout)
          return
        }

        setUsuario(dadosUsuario)

        // Carregar permissões do usuário
        const permissoesUsuario = await usuarioService.obterPermissoes(Number(id))
        if (!isMounted) return

        setPermissoesSelecionadas(permissoesUsuario || [])

        // Obter todas as permissões disponíveis
        // Usando as permissões por categoria definidas localmente
        const todasPermissoes = Object.values(permissoesPorCategoria)
          .flat()
          .map((p) => p.id)
        setPermissoes(todasPermissoes)

        if (loadTimeout) clearTimeout(loadTimeout)
        setCarregando(false)
      } catch (error) {
        if (isMounted) {
          console.error("Erro ao carregar dados:", error)
          setErro("Erro ao carregar dados. Tente novamente.")
          setCarregando(false)

          // Incrementar tentativas e tentar novamente se necessário
          const novasTentativas = tentativasCarregamento + 1
          setTentativasCarregamento(novasTentativas)

          if (novasTentativas < 3) {
            // Tentar novamente após um intervalo
            setTimeout(() => {
              if (isMounted) carregarDados()
            }, 2000)
          }
        }

        if (loadTimeout) clearTimeout(loadTimeout)
      }
    }

    carregarDados()

    return () => {
      isMounted = false
      if (loadTimeout) clearTimeout(loadTimeout)
      if (timeoutId) clearTimeout(timeoutId)
    }
  }, [id, hasPermission, timeoutId, tentativasCarregamento])

  const handleTogglePermissao = (permissaoId: string) => {
    setPermissoesSelecionadas((prev) => {
      if (prev.includes(permissaoId)) {
        return prev.filter((id) => id !== permissaoId)
      } else {
        return [...prev, permissaoId]
      }
    })
  }

  const handleSalvar = async () => {
    if (!id) return

    setSalvando(true)
    setErro(null)

    try {
      await usuarioService.atualizarPermissoes(Number(id), permissoesSelecionadas)
      toast({
        title: "Permissões atualizadas",
        description: "As permissões do usuário foram atualizadas com sucesso.",
      })
      router.push("/usuarios")
    } catch (error) {
      console.error("Erro ao salvar permissões:", error)
      setErro("Erro ao salvar permissões. Tente novamente.")
      toast({
        title: "Erro",
        description: "Não foi possível atualizar as permissões do usuário.",
        variant: "destructive",
      })
    } finally {
      setSalvando(false)
    }
  }

  const handleCancelarCarregamento = () => {
    if (timeoutId) clearTimeout(timeoutId)
    setCarregando(false)
    setErro("Carregamento cancelado pelo usuário.")
  }

  // Definição das permissões por categoria
  const permissoesPorCategoria = {
    Estoque: [
      { id: "ESTOQUE_VISUALIZAR", label: "Visualizar estoque" },
      { id: "ESTOQUE_ADICIONAR", label: "Adicionar produtos" },
      { id: "ESTOQUE_EDITAR", label: "Editar produtos" },
      { id: "ESTOQUE_REMOVER", label: "Remover produtos" },
    ],
    Vendas: [
      { id: "VENDAS_VISUALIZAR", label: "Visualizar vendas" },
      { id: "VENDAS_CRIAR", label: "Criar vendas" },
      { id: "VENDAS_CANCELAR", label: "Cancelar vendas" },
      { id: "VENDAS_RELATORIOS", label: "Relatórios de vendas" },
    ],
    Fila: [
      { id: "FILA_VISUALIZAR", label: "Visualizar fila" },
      { id: "FILA_ADICIONAR", label: "Adicionar clientes" },
      { id: "FILA_REMOVER", label: "Remover clientes" },
      { id: "FILA_GERENCIAR", label: "Gerenciar atendimentos" },
    ],
    Usuários: [
      { id: "USUARIOS_VISUALIZAR", label: "Visualizar usuários" },
      { id: "USUARIOS_ADICIONAR", label: "Adicionar usuários" },
      { id: "USUARIOS_EDITAR", label: "Editar usuários" },
      { id: "USUARIOS_REMOVER", label: "Remover usuários" },
      { id: "USUARIOS_PERMISSOES", label: "Gerenciar permissões" },
    ],
    Sistema: [
      { id: "CONFIGURACOES_VISUALIZAR", label: "Visualizar configurações" },
      { id: "CONFIGURACOES_EDITAR", label: "Editar configurações" },
      { id: "RELATORIOS_VISUALIZAR", label: "Visualizar relatórios" },
      { id: "BACKUP_GERENCIAR", label: "Gerenciar backups" },
    ],
  }

  if (!hasPermission("USUARIOS_PERMISSOES")) {
    return null
  }

  return (
    <div className="container mx-auto py-6">
      <div className="flex justify-between items-center mb-6">
        <div className="flex items-center gap-4">
          <BackButton />
          <h1 className="text-2xl font-bold">Gerenciar Permissões</h1>
        </div>
        {!carregando && !erro && (
          <Button onClick={handleSalvar} disabled={salvando}>
            {salvando ? "Salvando..." : "Salvar Permissões"}
          </Button>
        )}
      </div>

      {erro && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
          <p>{erro}</p>
          <Button
            variant="outline"
            className="mt-2"
            onClick={() => {
              setErro(null)
              setCarregando(true)
              setTentativasCarregamento(0)
              // Tentar carregar novamente
              const newTimeoutId = setTimeout(() => {
                window.location.reload()
              }, 500)
              setTimeoutId(newTimeoutId)
            }}
          >
            Tentar novamente
          </Button>
        </div>
      )}

      {carregando ? (
        <Card>
          <CardContent className="pt-6">
            <div className="flex flex-col items-center justify-center p-8">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900 mb-4"></div>
              <p className="text-lg">Carregando permissões...</p>
              <Button variant="outline" className="mt-4" onClick={handleCancelarCarregamento}>
                Cancelar
              </Button>
            </div>
          </CardContent>
        </Card>
      ) : (
        <>
          {usuario && (
            <Card className="mb-6">
              <CardHeader>
                <CardTitle>Usuário: {usuario.nome}</CardTitle>
                <CardDescription>Username: {usuario.username}</CardDescription>
              </CardHeader>
            </Card>
          )}

          {Object.entries(permissoesPorCategoria).map(([categoria, listaPermissoes]) => (
            <Card key={categoria} className="mb-6">
              <CardHeader>
                <CardTitle>{categoria}</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {listaPermissoes.map((permissao) => (
                    <div key={permissao.id} className="flex items-center space-x-2">
                      <Checkbox
                        id={permissao.id}
                        checked={permissoesSelecionadas.includes(permissao.id)}
                        onCheckedChange={() => handleTogglePermissao(permissao.id)}
                      />
                      <label
                        htmlFor={permissao.id}
                        className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                      >
                        {permissao.label}
                      </label>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          ))}

          {!carregando && !erro && (
            <div className="flex justify-end mt-6 space-x-4">
              <Button variant="outline" onClick={() => router.push("/usuarios")}>
                Cancelar
              </Button>
              <Button onClick={handleSalvar} disabled={salvando}>
                {salvando ? "Salvando..." : "Salvar Permissões"}
              </Button>
            </div>
          )}
        </>
      )}
    </div>
  )
}
